import React, { useState, useEffect }  from 'react';
import styled from 'styled-components';
import firebase from "firebase/app";
import 'firebase/database';

import Button from '@material-ui/core/Button';
import Picker, { SKIN_TONE_MEDIUM_DARK } from 'emoji-picker-react';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import { FIREBASE_CONFIG } from './config/global';

let database;
const DEFAULT_CONFIG = {
    name: '',
    color: 'black'
};
const InitDialog = ({ userConfig, setUserConfig }) => {
    const [open, setOpen] = useState(true);
    const { name, color } = userConfig;
    const handleClose = () => {
        setOpen(false);
    };

    return (
        <Dialog open={open}>
            <DialogTitle>Who are you?</DialogTitle>
            <DialogContent>
                <DialogContentText>
                Please input your name before chatting.
                </DialogContentText>
                <TextField
                    autoFocus
                    margin="dense"
                    value={name}
                    label="User Name"
                    fullWidth
                    onChange={(e) => { setUserConfig({ ...userConfig, name: e.target.value }) }}
                />
                <FormControl>
                    <InputLabel>Choose a color</InputLabel>
                    <Select
                        value={color}
                        onChange={(e) => { setUserConfig({ ...userConfig, color: e.target.value }) }}
                    >
                        <MenuItem value="black">Color: Black</MenuItem>
                        <MenuItem value="blue">Color: Blue</MenuItem>
                        <MenuItem value="red">Color: Red</MenuItem>
                        <MenuItem value="green">Color: Green</MenuItem>
                    </Select>
                </FormControl>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleClose} color="primary" disabled={!name}>
                    Confirm
                </Button>
            </DialogActions>
        </Dialog>
    )
};

const EmojiPicker = ({ show, setShowEmojiPicker, onEmojiClick }) => {
    const onClose = ()=> { setShowEmojiPicker(false) };
    return (
        <Dialog open={show} onClose={onClose}>
            <DialogContent>
                <Picker
                    onEmojiClick={onEmojiClick}
                    disableAutoFocus={true} 
                    skinTone={SKIN_TONE_MEDIUM_DARK}
                    groupNames={{smileys_people:"PEOPLE"}}
                />              
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="primary">
                    Close
                </Button>
            </DialogActions>
        </Dialog>
    );
};

function App() {
    const [value, setValue] = useState('');
    const [userConfig, setUserConfig] = useState(DEFAULT_CONFIG);
    const [message, setMessage] = useState([]);
    const [showEmojiPicker, setShowEmojiPicker] = useState(false);
    
    const { name:userName, color } = userConfig;

    const onInputValue = (e) => {
        const { value } = e.target;
        setValue(value);
    };

    const onSubmit = () => {
        database.push({
            user: userName,
            msg: value,
            time: (new Date().getTime())
        });
        setValue('');
    };


    const onKeyPress = (event) => {
        if (event.key === 'Enter') {
            event.preventDefault();
            event.stopPropagation();
            onSubmit();
        }
    }

    const onEmojiClick = (event, emojiObject) => {
        setValue(state => state + emojiObject.emoji);
    }

    const formatDisplay = (message) => {
        return message.map((msgValue, idx) => {
            const { time, msg, user } = msgValue;
            const message = `${user}(${formatTime(time)}): ${msg}`
            return <Message color={user === userName ? color : 'black'} key={idx}>{message}</Message>
        });
    };

    const updateScroll = () => {
        var element = document.querySelector('[data-display="DISPLAY_AREA"]');
        element.scrollTop = element.scrollHeight;
    };

    const formatTime = (time) => {
        const date = new Date(time);
        const month = date.getMonth() + 1;
        const day = date.getDate();
        const hh = date.getHours();
        const mm = date.getMinutes();
        const ss = date.getSeconds();
        return `${month}-${day} ${hh}:${mm}:${ss}`;
    };

    useEffect(() => {
        firebase.initializeApp(FIREBASE_CONFIG);
        database = firebase.database().ref('message/');
        database.on('child_added', function(snapshot) {
            const msgValue = snapshot.val();
            setMessage(state => [...state, msgValue]);
        });
    }, []);

    useEffect(() => {
        updateScroll();
    }, [message]);

    const displayMsg = formatDisplay(message);

    return (
        <Container>
            <InitDialog userConfig={userConfig} setUserConfig={setUserConfig} />
            <EmojiPicker show={showEmojiPicker} setShowEmojiPicker={setShowEmojiPicker} onEmojiClick={onEmojiClick} />
            <DisplayContainer>
                <DisplayContent data-display="DISPLAY_AREA">{displayMsg}</DisplayContent>
                <ActionContainer>
                    <UserName>User: {userName}</UserName>
                    <Input onChange={onInputValue} onKeyPress={onKeyPress} value={value} width={75} disabled={!userName}/>
                    <Button onClick={() => { setShowEmojiPicker(true) }}>ðŸ˜€</Button>
                    <Button onClick={onSubmit} color="primary">Submit</Button>
                </ActionContainer>
            </DisplayContainer>
        </Container>
    );
}

export default App;

export const Container = styled.div`
    display: flex;
    justify-content: center;
`;

export const DisplayContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-width: 300px;
    padding: 10px;
`;

export const ActionContainer = styled.div`
    display: flex;
`;

export const Message = styled.div`
    padding: 5px;
    color: ${({ color }) => color}
`;

export const UserName = styled.div`
    width: 150px;
`;

export const DisplayContent = styled.div`
    height: 85vh;
    overflow-y: auto;
    padding: 10px;
    border: 1px black solid;
`;

export const Input = styled.input`
    height: 25px;
    width: ${({ width }) => width}vw;
`;
